</template>

<script>


  import UserIntroduce from "../component/UserIntroduce";
  import UserPageCategory from "../component/UserPageCategory";
  import UserHotPage from "../component/UserHotPage";
  import UserPageSreach from "../component/UserPageSreach";


  export default {
        name: "UserSidePanel",
    components:{UserIntroduce,UserPageCategory,UserHotPage,UserPageSreach},
      props:{
        user_info:Object
      }
    }
</script>

<style scoped>
  .user-container{

  }

  .user-container *{

    margin-bottom: 10px;

  }

</style>



