<template class="login-page">
  <div class="login-page">
    <login-img></login-img>

    <login-panel v-if="getPath()==='/login'"></login-panel>
    <register-panel v-if="getPath()==='/register'"></register-panel>
  </div>
</template>

<script>
    import NavigationBar from "./component/NavigationBar";
    import LoginPanel from "./Panel/LoginPanel";
    import RegisterPanel from "./Panel/RegisterPanel";
    import LoginImg from "./component/img/loginImg";

    export default {
        name: "loginAndRegister",
      components: {LoginImg, RegisterPanel, LoginPanel, NavigationBar},
      methods:{
          getPath(){
            return this.$route.path
          }
      }

    }
</script>

<style scoped>
  .login-page{
  width: 100%;
  height: 100%;
}


</style>



