<template>
  <div class="login">

    <el-row>

      <el-col :span="4" :offset="10" style="margin-top: 100px">
        <el-card class="box-card" style="box-shadow: 0px 1px 2px lightgrey;min-width: 350px">
          <div slot="header" class="clearfix">
            <div style="font-size: large;font-weight: bold;padding: 10px 0px 10px 0px">登录</div>

          </div>
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item   prop="userAccount" label-width="0px">
              <el-input v-model="ruleForm.userAccount" placeholder="请输入账号"><template slot="prepend">  <i class="el-icon-s-custom"></i></template></el-input>
            </el-form-item>
            <el-form-item   prop="userPassword" label-width="0px">
              <el-input v-model="ruleForm.userPassword" placeholder="请输入密码"><template slot="prepend">  <i class="el-icon-lock"></i></template></el-input>
            </el-form-item>

          </el-form>

          <el-button type="primary"  @click="submitForm('ruleForm')" style="margin-top: 30px;width: 100%;margin-bottom: 20px">登录</el-button>
          <div style="margin-bottom: 20px">
            <el-link type="primary" style="float: right" href="/register"> 注册?</el-link>
            <el-link type="primary" style="float: left">忘记密码?</el-link>
          </div>
        </el-card>
      </el-col>

    </el-row>
  </div>
</template>

<script>
  export default {
    name: "LoginPanel",
    data() {
      return {
        ruleForm: {
          userAccount: '',
          userPassword: '',


        },
        rules: {
          userAccount: [
            { required: true, message: '请输入账号', trigger: 'blur' },
            { min: 2, max: 16, message: '长度在 2 到 16 个字符', trigger: 'blur' }
          ],
          userPassword: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
          ]

        }
      };
    },
    methods: {

      //消息提示
      messageAlert(msg, type) {
        this.$message({
          message: msg,
          type: type
        });
      },

      //基础axios
      getBaseAxios(url, success, failed,error) {
        let _this = this
        this.$axios({
          method: 'get',
          url: url,
        })
          .then(function (r) {
            if (r.data.code == 1) {
              _this.messageAlert(r.data.message, "success")
              if (success!=undefined) success(r.data);
            } else {
              _this.messageAlert(r.data.message, "error")
              if (failed!=undefined)failed(r.data)
            }
          })
          .catch(function (e) {
            _this.messageAlert("未知的错误，请重新操作!", "error")
            if (error!=undefined)error(e)
          });
      },

      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.getBaseAxios(this.urlUserLogin(this.ruleForm.userAccount,this.ruleForm.userPassword),function () {
              setTimeout(()=>{
                window.location.href = "/user"
              },1000)
            });

          } else {
            this.errorShake()
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      errorShake(){
        anime({
          targets: '.el-form-item__error',
          translateX: [0,10],
          duration:100,
          direction: 'alternate',
        });
      }
    }
  }
</script>

<style scoped>

</style>



