<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix blog-page-card-header">
        <el-row :gutter="20">
          <el-col :span="6"><img src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
                                 style="width: 70px;border-radius: 50%"></el-col>
          <el-col :span="18" style="margin-top: 15px">
            <el-row>
              <el-col style="font-size: small">{{user_info.name}}</el-col>
            </el-row>
            <el-row style="margin-top: 10px">
              <el-col style="color: grey;font-size: x-small">文章总数: <span style="font-weight: bold">{{user_info.all_page_count}}</span>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </div>


      <user-info :user_info="user_info"></user-info>
    </el-card>


  </div>
</template>

<script>
  import UserPageCategory from "./UserPageCategory";
  import UserInfo from "./UserInfo";

  export default {
    name: "UserIntroduce",
    components: {UserInfo, UserPageCategory},
    props: {
      user_info: Object,
    }
  }
</script>

<style scoped>

</style>



