<template>
  <div class="fall-parent">
    <NavigationBar v-if="getPath()!='/user/editor'"></NavigationBar>
    <div id="app" >
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import NavigationBar from "./views/public/component/NavigationBar";
export default {

  name: 'App',
  components: {NavigationBar},
  methods:{
    getPath(){

      return this.$route.path
    }
  }
}
</script>



