<template>
  <!--如果存在子控件，则遍历子控件，否则显示name-->

  <el-submenu :index="id" v-if="list.children" class="user-list"  >

    <template v-if="list.name" slot="title">
      <span>
        <el-avatar v-if="list.name==='用户'"
                   src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
      <i v-else :class="list.icon_class"></i>
      {{user}}
      </span>
    </template>
    <list v-for="(item, index) in list.children" :key="index" :id="id+'-'+index" :list="item" style=""></list>

  </el-submenu>
  <!--显示搜索框-->
  <el-menu-item v-else-if="list.name==='搜索'">
    <SearchInput></SearchInput>
  </el-menu-item>

  <!--消息框-->
  <el-menu-item v-else-if="list.name==='消息'" @click="get(list.url)"  class="message"  style="margin-left: 15%">
    <el-badge :value="messageCount" :max="99" class="item list-item ">
      <i :class="list.icon_class"></i>
      {{list.name}}
    </el-badge>
  </el-menu-item>

  <!--只显示name和icon-->
  <el-menu-item :index="id" @click="get(list.url)" v-else shadow="hover"   >
    <i :class="list.icon_class"></i>
    {{list.name}}
  </el-menu-item>

</template>
<script>
  import SearchInput from "../../public/component/SearchInput";
  import Vue from 'vue'

  export default {
    name: "List",
    components: {SearchInput},
    props: {
      list: Object,
      id: String,
      messageCount: Number,
      user: String
    },
    data() {
      return {

      }
    },
    methods: {
      get(url) {
        console.log(url)
        window.location.href = url;
      },
      isOffset(id) {

        return id == 0 ? 10 : 0
      }
    }
  };
</script>

<style scoped>
  /*不要乱改下面样式，定好了的*/

  .nav li /deep/ .el-menu-item {
    height: 40px;
    line-height: 40px;



  }

  .user-list {
    line-height: 40px;
    height: 40px;
    margin-top: -5px;
    margin-left: 10px;
  }

  .user-list * {
    line-height: 40px;
    height: 40px;

  }

  .nav li .list-item {
    height: 20px;
    line-height: 20px;
  }
</style>



