<template>
    <div class="tools-list" >
      <el-card v-for="data in datas" :key="index" >
          <div slot="header">{{data.name}}</div>
          <div v-html="data.html"></div>
      </el-card>



    </div>
</template>

<script>

  let datas=[
    {
      name: '热门文章',
      html: '<h1>阿斯达</h1>'
    },
    {
      name: '热门标签',

    },
    {
      name: '推荐',

    },
    {
      name: '关注',

    },


  ]


    export default {
        name: "ToolsSideBar",
        data(){
          return{
            datas:datas
          }
        }
    }
</script>

<style scoped>
.tools-list  .el-card{
  font-size: x-small;
  text-align: left;
  margin-bottom: 20px;

}


</style>



