import Vue from 'vue'
import Router from 'vue-router'




import pageNotFound from '../views/error/pageNotFound'
import App from '../App'

import MainContain from "../views/index/component/MainContain";
import LoginAndRegister from "../views/public/loginAndRegister";

import UserPanel from "../views/user/panel/UserPanel";
import MainPage from "../views/public/mainPage";
import Editor from '../views/user/editor/Editor'


Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path:'/', component: MainContain
    },
    {
      path:'/index', component: MainContain
    },
    {
      path:'/login', component: LoginAndRegister
    },
    {
      path:'/register', component:LoginAndRegister
    },

    {
      path:'/user', component: UserPanel
    },
    {
      path:'/user/editor', component: Editor
    },
    {
      path:'/page', component : MainPage
    },
    {
      path:'/route/test', component : App
    },
    {
      path:'*', component: pageNotFound
    }

  ]
})



