<!--文章分类侧边栏-->
<template>
  <div >
    <el-card class="card">
      <div  slot="header" style="font-weight: bold;font-size: 16px;height: 20px" >文章分类</div>
      <card v-for="(list,index) in class_list" :key="index"  @click.native="get(list.url)">{{list.name}}</card>
    </el-card>


  </div>
</template>
<script>
  let class_list=[
    {
      name: '阿斯达1111',
      url:'https://element.eleme.cn/#/zh-CN/component/card'
    },
    {
      name: '阿斯达',
      url:'/index'
    }
    ,{
      name: '阿斯达',
      url:'/index'
    }
    ,{
      name: '阿斯达',
      url:'/index'
    }

  ];

  export default {
    name: "PageClassSide",
    components: {
      card: {
        template: '<div><slot></slot></div>'
      }
    },
    data() {
      return {
        class_list:class_list,
        tabPosition: 'left'
      };
    },
    methods:{
      get(url){
        console.log(url)
        window.location.href=url;
      }
    }
  };
</script>

<style scoped>
  .card *:hover {
    cursor: pointer;
    color: #409EFF;
  }
  .card{
    background-color: white;
  }

  .card * {
    font-size: 16px;
    width: 100%;
    height: 40px;

  }


</style>



