<template>
  <div class="page-card-div" @mouseover="enter()" @mouseleave="leave()" >
    <el-row  >
      <el-col :span="20">

        <div style="position: relative;z-index: 9999" >
          <el-card class="box-card" id="user-div ">
            <div slot="header" class="clearfix blog-page-card-header">
              <el-row>
                <el-col :span="20">
                  <div class="blog-page-card-title" style="font-weight: normal">{{blogpage.page_info.title}}</div>
                </el-col>
                <el-col :span="4" class="list-i">
                  <i class="el-icon-view"> {{blogpage.page_info.read_num}}&nbsp;&nbsp;</i>
                  <i class="el-icon-star-off"> {{blogpage.page_info.star_num}}&nbsp;&nbsp;</i>
                  <i class="el-icon-chat-line-square"> {{blogpage.page_info.reply_num}}&nbsp;&nbsp;</i>
                </el-col>


              </el-row>


            </div>
            <div class="blog-page-card-intro el-row" style="top: -10px;position: relative;">
              <!--显示100个字以内的信息-->
              {{blogpage.page_info.content.length>40?blogpage.page_info.content.substring(0,100)+'......':blogpage.page_info.content}}
            </div>
          </el-card>
        </div>

      </el-col>

      <el-col :span="2" class="box-card">
        <el-row>
          <el-button type="primary" icon="el-icon-edit" circle  :style="editor_btn"   :class="'page-card-btn-'+id"></el-button>
        </el-row>
        <el-row>
          <el-button type="danger" icon="el-icon-delete" circle :style="editor_btn" :class="'page-card-btn-'+id"></el-button>
        </el-row>
        <el-row>
          <el-button icon="el-icon-upload2" circle :style="editor_btn" :class="'page-card-btn-'+id"></el-button>
        </el-row>
      </el-col>

    </el-row>
  </div>
</template>

<script>

  Vue.nextTick(function () {
    console.log("文章加载完成！");

    anime({
      targets: '.page-card-div',
      translateX: 1500,
      direction: 'reverse',
      duration:700,
      easing: 'easeInOutSine',
      delay: anime.stagger(50) // 每个元素的延迟增加100毫秒。
    });
  })

  export default {
    name: "PageCard",
    props: {
      blogpage: Object,
      id: Number

    },
    methods: {



      enter() {
        if(this.animate_status==0){
          this.animate_status=1
          let ent = anime({
            targets: '.page-card-btn-' + this.id,
            translateX: 70,
            duration: 100,
            delay: anime.stagger(50),
            begin: function (anim) {
              for (let i in anim.animatables) {
                anim.animatables[i].target.style.display="inline";
              }
            }
          });
        }
      },
      leave() {

        if(this.animate_status==1){
          let that = this;
          let lea = anime({
            targets: '.page-card-btn-' + this.id,
            translateX: 0,
            duration: 100,
            delay: anime.stagger(50),
            complete: function (anim) {
              for (let i in anim.animatables) {
                anim.animatables[i].target.style.display="none";
              }
              that.animate_status=0
            }
          });


        }
      }
    },
    data() {
      return {

        animate_status : 0,


        editor_btn: {
          width: '48px',
          height: ' 48px',
          position: 'relative',
          'margin-right': '120%',
          float: 'right',
          display:'none',

        },
        card_editor: 'block',

      }
    }
  }
</script>

<style scoped>

  .box-card {
    margin: 5px 5px 5px 5px;

  }

  .blog-page-card-header {
    height: 20px;
  }

  .blog-page-card-title {
    text-align: left;
    font-weight: bold;

  }

  .list-i * {
    font-size: x-small;
    cursor: pointer;
    color: #6e92b7;
  }



  .blog-page-card-intro {
    text-align: left;
    height: 45px;
    font-size: x-small;
    color: #8c939d;
  }

</style>



