<template>
  <div class="register">

    <el-row>

      <el-col :span="6" :offset="9" style="margin-top: 100px">
        <el-card class="box-card" style="box-shadow: 0px 1px 2px lightgrey;min-width: 350px">
          <div slot="header" class="clearfix">
            <div style="font-size: large;font-weight: bold;padding: 10px 0px 10px 0px">注册</div>

          </div>
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item prop="userAccount" label-width="0px">
              <el-input v-model="ruleForm.userAccount" placeholder="请输入账号">
                <template slot="prepend"><i class="el-icon-s-custom"></i></template>
              </el-input>
            </el-form-item>
            <el-form-item prop="userPassword" label-width="0px">
              <el-input v-model="ruleForm.userPassword" placeholder="请输入密码">
                <template slot="prepend"><i class="el-icon-lock"></i></template>
              </el-input>
            </el-form-item>
            <el-form-item prop="userEmail" label-width="0px">
              <el-row>

                <el-col :span="24">
                  <el-input v-model="ruleForm.userEmail" placeholder="qq 邮箱 (便于找回密码)">
                    <template slot="prepend"><i class="el-icon-message"></i></template>

                  </el-input>
                </el-col>
                <!--<el-col :span="7">-->

                  <!--<el-select v-model="value" :placeholder="value">-->
                    <!--<el-option-->
                      <!--v-for="item in options"-->
                      <!--:key="item.value"-->
                      <!--:label="item.label"-->
                      <!--:value="item.value">-->
                    <!--</el-option>-->
                  <!--</el-select>-->
                <!--</el-col>-->
              </el-row>
            </el-form-item>

            <el-form-item prop="emailCode" label-width="0px">
              <el-input   v-model="ruleForm.emailCode" placeholder="6位数验证码，确保账号正确">
                <i slot="suffix" v-show="emailVcodeInput.show" :style="{color:emailVcodeInput.color}"
                   :class="emailVcodeInput.icon"></i>
                <template slot="prepend">
                  <el-button id="send_email_ver" :disabled="send_email_btn.disabled"
                             v-text="!isNaN(send_email_btn.text)?send_email_btn.text+' s':send_email_btn.text"
                             @click="sendEmailVerification('ruleForm')"></el-button>
                </template>
                <template slot="append">
                  <el-button @click="emailCodeForm('ruleForm',checkEmailVcode)">验证</el-button>
                </template>
              </el-input>
            </el-form-item>

          </el-form>


          <el-button :type="emailVcodeInputIsCorrect()?'primary':''" @click="submitForm('ruleForm',userRegister)"
                     :correct="emailVcodeInputIsCorrect()"
                     :title="emailVcodeInputIsCorrect()?'注册':'必须所有信息填写正确才能注册！'"
                     :disabled="emailVcodeInputIsCorrect()?false:'disable'"
                     style="margin-top: 30px;width: 100%;margin-bottom: 20px">注册
          </el-button>
          <div style="margin-bottom: 20px">
            <el-link type="primary" style="float: left" @click="resetForm('ruleForm')">重置</el-link>
            <el-link type="primary" style="float: right" href="/login"> 已经有账号?</el-link>
          </div>

        </el-card>
      </el-col>

    </el-row>
  </div>
</template>

<script>
  Vue.nextTick(function () {
  });



  export default {
    name: "RegisterPanel",
    data() {
      return {

        options: [{
          value: '@qq.com',
          label: '@qq.com'
        }

        ],
        value: '@qq.com',


        emailVcodeInput: {
          correct: false,
          show: false,
          color: 'green',
          icon: 'el-input__icon el-icon-circle-check',
        },

        send_email_btn: {

          disabled: false,
          text: "发送验证码"
        },

        ruleForm: {
          userAccount: '',
          userPassword: '',
          userEmail: '',
          emailCode: '',

        },
        rules: {
          userAccount: [
            {required: true, message: '请输入账号', trigger: 'blur'},
            {min: 2, max: 12, message: '长度在 2 到 12 个字符', trigger: 'blur'}
          ],
          userPassword: [
            {required: true, message: '请输入密码', trigger: 'blur'},
            {min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur'}
          ],

          userEmail: [
            {required: true, message: '请输入qq邮箱地址', trigger: 'blur'},
          ],
          emailCode: [
            {required: true, message: '请输6位数的验证码', trigger: 'blur'},
            {min: 6, max: 6, message: '请输6位数的验证码', trigger: ['blur', 'change']}
          ]
        }
      };
    },
    methods: {

      emailVcodeInputIsCorrect() {
        return this.emailVcodeInput.correct === true
      },

      //消息提示
      messageAlert(msg, type) {
        this.$message({
          message: msg,
          type: type
        });
      },

      //刷新发送验证码的按钮状态
      freshEmailBtnStatus(msg) {
        this.send_email_btn.disabled = this.send_email_btn.disabled === false ? "disable" : false
        if (msg != undefined) {
          this.send_email_btn.text = this.send_email_btn.disabled === false ? msg : 0
        } else {
          this.send_email_btn.text = this.send_email_btn.disabled === false ? "发送验证码" : 0
        }


      },

      //检查验证码是否正确
      checkEmailVcode() {

        if(isNaN(this.ruleForm.emailCode)){
          this.messageAlert("请输入纯数字的验证码！","error")
          return;
        }

        let _this = this
        this.getBaseAxios(this.urlCheckEmailVode(_this.ruleForm.emailCode),function () {
          _this.emailVcodeInput.correct = true
        },function () {
          _this.emailVcodeInput.correct = false
        });
      },

      //发送邮箱验证码
      sendEmailVerification(formName) {
        let validate_count = 0;
        this.$refs[formName].validateField(["userAccount","userPassword","userEmail"],(msg) => {

          if (validate_count===2&&msg==="") {
            let _this = this

            this.messageAlert("发送邮件中，将在一分钟内发送，请在邮箱中查收...", "warning")

            _this.freshEmailBtnStatus()

            this.getBaseAxios(this.urlEmailVerification(_this.ruleForm.userEmail),undefined,function () {
              _this.freshEmailBtnStatus("重新发送")
            },function () {
              _this.freshEmailBtnStatus("重新发送")
            })


            anime({
              targets: this.send_email_btn,
              text: [60, 0],
              round: 1,
              duration: 60 * 1000,
              easing: 'linear',
              complete: function (anim) {
                if (_this.send_email_btn.disabled != false) {
                  _this.freshEmailBtnStatus()
                }
              }
            });
          }

          else if (msg==="") {
            validate_count++;
          }  else {
            this.errorShake()
            return false;
          }
        });


      },

      //用户注册
      userRegister() {
        let _this = this
        this.getBaseAxios(this.urlUserRegister(_this.ruleForm.userAccount, _this.ruleForm.userPassword, _this.ruleForm.userEmail),function () {
          window.location.href = "/user"
        });

      },

      //检验邮箱验证码正确性
      emailCodeForm(formName, fn) {

        this.$refs[formName].validateField("emailCode", (msg) => {
          if (msg !== "请输6位数的验证码") {
            if (fn !== undefined) {
              console.log(fn)
              fn()
            }
          } else {
            this.errorShake()
            return false;
          }
        });

      },

      callback(){

      },

      //基础axios
      getBaseAxios(url, success, failed,error) {
        let _this = this
        this.$axios({
          method: 'get',
          url: url,
        })
          .then(function (r) {
            if (r.data.code == 1) {
              _this.messageAlert(r.data.message, "success")
              if (success!=undefined) success(r.data);
            } else {
              _this.messageAlert(r.data.message, "error")
              if (failed!=undefined)failed(r.data)
            }
          })
          .catch(function (e) {
            _this.messageAlert("未知的错误，请重新操作!", "error")
            if (error!=undefined)error(e)
          });
      },

      //检验表单
      submitForm(formName, fn) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (fn != undefined) {
              console.log(fn)
              fn()
            }
          } else {
            this.errorShake()
            return false;
          }
        });
      },

      //重置表单
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },

      errorShake(){
        anime({
          targets: '.el-form-item__error',
          translateX: [0,10],
          duration:100,
          direction: 'alternate',
        });
      }


    }
  }
</script>

<style scoped>

</style>



