<template>
  <div class="fall-parent">

    <!--<mavon-editor-->
    <!--:toolbars="toolbars"        //指定工具栏-->
    <!--@imgAdd="handleEditorImgAdd"     //指定图片上传调用的方法，该方法主要将图片上传后台，并且返回地址，替换到markdown中-->
    <!--@imgDel="handleEditorImgDel"    //删除图片调用的方法，该方法主要调用后台删除图片-->
    <!--style="height:600px"         //-->
    <!--v-model="value"         //绑定 value 值  必须的-->
    <!--@change="change"         //监听markdown输入 ，可以实时保存等等。。-->
    <!--ref=md                //指定一个用来引用markdown的 可以是任意字符串-->
    <!--/>-->
    <el-row class="editor-content">
      <el-col>

        <el-row style="height: 80px">
          <el-col :span="22" :offset="1">
            <el-card class="box-card" >
              <el-row>

                <el-col :span="22">
                  <el-input  placeholder="请输入内容">
                    <template  slot="prepend" >文章标题</template>
                  </el-input>

                </el-col>

                <el-col :span="2">
                  <el-button type="primary">发布文章</el-button>
                </el-col>

              </el-row>
            </el-card>
          </el-col>
        </el-row>


        <el-row style="margin-top: 10px">
          <el-col :span="22" :offset="1">

            <mavon-editor
              :toolbars="toolbars"
              @imgAdd="handleEditorImgAdd"
              @imgDel="handleEditorImgDel"
              style="height:100%"
              v-model="value"
              @change="change"
              ref=md
              class="fall-parent"
            />
          </el-col>
        </el-row>

        <el-row style="margin-top: 10px;height: 30px">
          <el-col>
            Markdown 编辑器 ---    <el-link type="primary" href="https://github.com/hinesboy/mavonEditor" target="_blank"   style="font-size: medium">@mavon markdown</el-link>
          </el-col>
        </el-row>


      </el-col>
    </el-row>


  </div>
</template>

<script>




  export default {
    name: "editor",
    mounted () {
      //第一种方法
      if(window.name == ""){
      window.name = "isReload";  // 在首次进入页面时我们可以给window.name设置一个固定值
      }else if(window.name == "isReload"){
        this.openMessage();
      }

    },

    methods: {
      openMessage() {
        this.$message({
          message: '输入图片——直接复制粘贴即可！',
          showClose: true,
          type: 'warning'
        });
      },
    },
    data() {
      return {

        value: '',

        toolbars: {
          bold: true,
          italic: true, // 斜体
          header: true, // 标题
          underline: true, // 下划线
          strikethrough: true, // 中划线
          mark: true, // 标记
          superscript: true, // 上角标
          subscript: true, // 下角标
          quote: true, // 引用
          ol: true, // 有序列表
          ul: true, // 无序列表
          link: true, // 链接
          imagelink: true, // 图片链接
          code: false, // code
          table: true, // 表格
          fullscreen: true, // 全屏编辑
          readmodel: true, // 沉浸式阅读
          htmlcode: true, // 展示html源码
          help: true, // 帮助
          /* 1.3.5 */
          undo: true, // 上一步
          redo: true, // 下一步
          trash: true, // 清空
          save: true, // 保存（触发events中的save事件）
          /* 1.4.2 */
          navigation: true, // 导航目录
          /* 2.1.8 */
          alignleft: true, // 左对齐
          aligncenter: true, // 居中
          alignright: true, // 右对齐
          /* 2.2.1 */
          subfield: true, // 单双栏模式
          preview: true, // 预览
        }
      }
    }
  }
</script>

<style scoped>

  /*编写容器的一些css，根据需要进行调整，这里是我博客的，在github提供的.markdown-body基础上修改的*/
  .markdown-body {
    box-sizing: border-box;
    min-width: 200px;
    /* max-width: 980px; */
    /* padding: 45px; */
    box-shadow: 2px 4px 6px gray;

  }
  .editor-content{
    height: 100%;
  }

  .editor-content *{
    height: 100%;
  }


  .v-note-edit{
    box-shadow: 2px 4px 6px gray;
  }

  /*github使用的是这个   根据自己的进行调整*/
  .markdown-body {



    margin: 0 auto;
    padding: 45px;
  }

  /*//这个要配合移动端 不是很理解*/
  @media (max-width: 767px) {
    .markdown-body {
      padding: 15px;
    }
  }
</style>



