




exports.install = function (Vue, options) {



  Vue.prototype.urlCheckEmailVode = function(user_email){
    return '/api/user/email/verification/check/code/' + user_email
  }

  Vue.prototype.urlEmailVerification = function(user_email){
    return '/api/user/email/verification/' + user_email
  }

  Vue.prototype.urlUserLogin = function(user_account, user_password){
    return '/api/user/login/' + user_account + '/' + user_password
  }

  Vue.prototype.urlUserRegister = function(user_account, user_password, user_email){
    return '/api/user/register/' + user_account + '/' + user_password + '/' + user_email
  }
};




